"use client";

import type { Question, Trait } from "@/lib/types";
import { Button } from "./ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "./ui/card";
import { Progress } from "./ui/progress";
import { motion } from "framer-motion";

interface QuizQuestionsProps {
  question: Question;
  onAnswer: (questionId: number, trait: Trait) => void;
  progress: number;
  totalQuestions: number;
}

const variants = {
  initial: { opacity: 0, x: 50 },
  animate: { opacity: 1, x: 0 },
  exit: { opacity: 0, x: -50 },
};

export default function QuizQuestions({ question, onAnswer, progress, totalQuestions }: QuizQuestionsProps) {
  const Icon = question.icon;

  return (
    <motion.div
      key={question.id}
      variants={variants}
      initial="initial"
      animate="animate"
      exit="exit"
      transition={{ duration: 0.3 }}
    >
      <Progress value={progress} className="mb-4" />
      <Card className="glowing-card">
        <CardHeader className="text-center">
            <div className="mx-auto bg-primary/10 border border-primary/20 p-3 rounded-full w-fit mb-2">
                <Icon className="w-8 h-8 text-primary" />
            </div>
          <CardTitle className="text-2xl">{question.text}</CardTitle>
          <CardDescription>Question {question.id} of {totalQuestions}</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mt-4">
            {question.options.map((option, index) => (
              <Button
                key={index}
                variant="outline"
                size="lg"
                className="h-auto py-4 whitespace-normal justify-start text-left"
                onClick={() => onAnswer(question.id, option.trait)}
              >
                {option.text}
              </Button>
            ))}
          </div>
        </CardContent>
      </Card>
    </motion.div>
  );
}
